import { list } from "@/config/examples/children";

export default {
  name: "childrens",
  data: () => ({
    searchText: "",
  }),
  computed: {
    childId() {
      return this.$route.params.id;
    },
    children() {
      return list.filter(child => child.name.toLowerCase().includes(this.searchText.toLowerCase()));
    }
  }
}
